g,\n.recharts-wrapper {\n    min-width: auto;\n}\n\n.jp-ReactWidget {\n    color: var(--jp-ui-font-color1);\n    background: var(--jp-layout-color1);\n    font-size: 48px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n}\n\n.selected-config,\n.MuiTableRow-root.Mui-selected {\n    background-color: var(--jp-brand-color1) !important;\n    fill: var(--jp-brand-color1) !important;\n}\n\ntr.MuiTableRow-root.MuiTableRow-hover:hover {\n    background-color: var(--hover-color);\n}\n\n.MuiTableContainer-root {\n    overflow-y: hidden;\n}\n\n.jp-Cell-outputWrapper .MuiTabPanel-root {\n    padding: 5px 0 0;\n}\n\n.MuiTab-root.tab-small {\n    font-size: var(--jp-code-font-size);\n    min-height: 0;\n    padding: 2px 4px;\n}\n\n.container {\n    border: 1px solid #c2c2c2;\n    /*box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);*/\n    padding: 10px;\n    border-radius: 4px;\n    margin: 1px 1px 9px;\n}\n\n.container h3,\n.container h4 {\n    margin-top: 0.3rem;\n    margin-bottom: 0.3rem;\n}\n\n.hyperlink {\n    color: #106BA3;\n}\n\n.collapse-tooltip {\n    white-space: pre-line;\n    text-align: justify;\n    font-size: 13px;\n    padding: 5px;\n    max-width: 400px;\n    font-family: \"Roboto\", \"Helvetica\", \"Arial\", sans-serif;\n    font-weight: 400;\n    line-height: 1.43;\n    letter-spacing: 0.01071em;\n}\n\n.custom-header {\n    display: flex;\n    justify-content: space-between;\n}\n\n.custom-header .MuiSvgIcon-root {\n    top: 0;\n}\n\n.key-value-pair .MuiSvgIcon-root {\n    top: 0.25em;\n}\n\n.flex-column {\n    display: flex;\n    flex-direction: column;\n}\n\n.flex-column > *:last-child {\n    margin-top: auto;\n}\n\n/** Flexible SVG */\n.flexible-svg-container {\n    display: inline-block;\n    position: relative;\n    width: 99%;\n    vertical-align: middle;\n}\n\n.flexible-svg {\n    display: block;\n    position: absolute;\n    overflow: visible;\n    top: 0;\n    left: 0;\n}\n\n.recharts-surface {\n    overflow: visible;\n}\n\n\n/** Hierarchical Tree */\n.hierarchical-tree_node-container {\n    display: flex;\n    align-items: center;\n    height: 100%;\n    width: 100%;\n    font: 10px sans-serif;\n}\n\n.hierarchical-tree_node-content {\n    cursor: pointer;\n    border: 1px solid var(--border-color);\n    border-radius: 5px;\n    padding: 2px 5px;\n    width: 100%;\n    overflow: hidden;\n    text-align: center;\n    text-overflow: ellipsis;\n    max-height: 100%;\n    box-sizing: border-box;\n}\n\n.selected .hierarchical-tree_node-content {\n    background-color: var(--jp-brand-color1) !important;\n    border-width: 2px;\n}\n\n.highlight.structure-graph_node .hierarchical-tree_node-content {\n    background-color: var(--hover-color);\n    border-width: 2px;\n}\n\n.hierarchical-tree_node-content-virtual {\n    height: 100%;\n    padding: 0;\n    box-sizing: border-box;\n}\n\n.hierarchical-tree_node-content-splitter .hierarchical-tree_node-content {\n    border-right-width: 10px !important;\n}\n\n.hierarchical-tree_node-content-merger .hierarchical-tree_node-content {\n    border-left-width: 10px !important;\n}\n\n.hierarchical-tree_node-content h3 {\n    margin: 0;\n    padding: 5px 0;\n}\n\n.hierarchical-tree_link {\n    fill: none;\n    stroke: var(--border-color);\n    stroke-width: 2px;\n}\n\n.hierarchical-tree_link.selected {\n    stroke-width: 4px;\n}\n\n.hierarchical-tree_link-label {\n    font: 10px sans-serif;\n    stroke-width: 1px;\n}\n\n/** MCTS Explanation */\n.mcts-explanation .hierarchical-tree_node-content {\n    font-size: 8px;\n}\n\n.mcts-explanation_node-details {\n    display: grid;\n    text-align: left;\n    grid-template-columns: 33% 33% 33%;\n    font-size: 8px;\n}\n\n.mcts-explanation.highlight .hierarchical-tree_node-content {\n    background-color: var(--hover-color);\n}\n\n.failure .hierarchical-tree_node-content {\n    cursor: default;\n}\n\n.expandable .hierarchical-tree_node-content {\n    background-color: var(--selectable-color);\n}\n\n.expandable .hierarchical-tree_node-content:hover {\n    background-color: var(--hover-color);\n    border-width: 2px;\n}\n\n\n/** Candidate Table */\n.MuiDataGrid-cell:focus {\n    outline: none !important;\n}\n\n.candidate-table_visually-hidden {\n    border: 0;\n    clip: rect(0 0 0 0);\n    height: 1px;\n    margin: -1px;\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    top: 20px;\n    width: 1px;\n}\n\n\n/** Structure Graph */\n.structure-graph_end-node {\n    width: 100%;\n    height: 100%;\n    display: block;\n}\n\n.structure-graph_node .hierarchical-tree_node-content {\n    background-color: var(--selectable-color);\n}\n\n.structure-graph_node .hierarchical-tree_node-content:hover {\n    background-color: var(--hover-color);\n    border-width: 2px;\n}\n\n.structure-graph_node.selected .hierarchical-tree_node-content {\n    border-width: 2px;\n}\n\ndiv.structure-graph_tooltip {\n    max-width: 800px;\n    background-color: white;\n    color: rgba(0, 0, 0, 0.87);\n\n    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);\n    padding: 5px 20px;\n    border-radius: 4px;\n    font-weight: initial;\n}\n\n.structure-graph_tooltip .MuiTable-root {\n    width: initial;\n}\n\n.jp-Cell-outputWrapper .MuiTableCell-root,\n.jp-Cell-outputWrapper .MuiCollapse-wrapperInner {\n    font-size: var(--jp-code-font-size);\n}\n\n.jp-Cell-outputWrapper .MuiTableCell-sizeSmall,\n.jp-Cell-outputWrapper .MuiTableCell-sizeSmall.MuiTableCell-paddingCheckbox {\n    padding: 6px;\n}\n\n.jp-Cell-outputWrapper .MuiTableCell-sizeSmall:last-child {\n    padding: 6px 0;\n}\n\n\n/** Raw Dataset */\n.raw-dataset tr {\n    cursor: pointer;\n}\n\n.raw-dataset tr th:nth-last-child(3),\n.raw-dataset tr td:nth-last-child(3) {\n    border-left: var(--jp-border-width) solid var(--jp-border-color1);\n    padding-left: 10px;\n    font-style: italic;\n}\n\n.raw-dataset tr th:nth-last-child(4),\n.raw-dataset tr td:nth-last-child(4) {\n    padding-right: 30px;\n}\n\n\n/** JupyterButton */\n.jupyter-button {\n    max-width: 135px;\n}\n\n.jupyter-button > span {\n    font-size: var(--jp-code-font-size);\n}\n\n.jupyter-button svg {\n    height: 1.75em;\n    vertical-align: bottom;\n}\n\n\n/** LIME */\n.lime {\n    display: flex;\n}\n\n.lime.vertical {\n    height: 100%;\n    flex-direction: column;\n}\n\n.lime.horizontal {\n    flex-direction: row;\n}\n\n.lime.vertical .lime_plot {\n    min-width: 350px;\n}\n\n.lime.horizontal .lime_plot {\n    flex-grow: 1;\n}\n\n.lime .rv-xy-plot__axis__title text {\n    font-size: larger;\n}\n\n.lime-class .recharts-rectangle:hover,\n.lime .recharts-cartesian-axis-tick-value {\n    cursor: pointer;\n}\n\n.lime-explanation .recharts-rectangle:hover {\n    cursor: default;\n}\n\n/** fANOVA */\n.selected-header {\n    font-weight: bold;\n}\n\n\n/** Global Surrogate */\n.global-surrogate_node .hierarchical-tree_node-container {\n    cursor: default;\n}\n\n.global-surrogate_additional-feature .hierarchical-tree_node-content {\n    color: var(--additional-feature-color);\n}\n\n\n/** Parallel Coordinates */\n.pc-border {\n    fill: rgba(255, 255, 255, 0.85);\n    stroke: #DFE3E6;\n    stroke-width: 1px;\n    pointer-events: all;\n    cursor: pointer;\n}\n\n.pc-choice > .pc-border:hover {\n    stroke: var(--jp-brand-color1) !important;\n    fill: rgba(255, 255, 255, 0.4);\n    stroke-width: 1px;\n}\n\n.pc-choice > circle {\n    fill: none;\n    stroke: rgb(108, 117, 125);\n    stroke-width: 1px;\n    pointer-events: all;\n}\n\n.pc-choice.selected > circle {\n    fill: #777;\n    fill-opacity: 0.3;\n    stroke-width: 2px;\n}\n\n.pc-choice > text {\n    font-size: 11px;\n    dominant-baseline: middle;\n    text-anchor: middle;\n}\n\n.pc-choice-expandable > circle {\n    fill: var(--selectable-color);\n}\n\n.pc-choice-expandable {\n    cursor: pointer;\n}\n\n.pc-choice-expandable:hover > circle {\n    fill: var(--hover-color);\n    stroke-width: 2px;\n}\n\n.pc-axis > path {\n    stroke: #b8b8b8;\n    stroke-width: 1px;\n    pointer-events: none;\n}\n\ntext.pc-axis-tick {\n    font-size: 11px;\n    fill: #b8b8b8;\n}\n\ntext.pc-axis-y-tick {\n    dominant-baseline: middle;\n    text-anchor: end;\n}\n\ntext.pc-axis-x-tick {\n    dominant-baseline: hanging;\n    text-anchor: middle;\n}\n\ntext.pc-axis-label {\n    cursor: pointer;\n}\n\ntext.pc-axis-label.selected {\n    font-weight: bold;\n    fill: var(--select-color)\n}\n\ntext.pc-axis-label:hover {\n    font-weight: bold;\n    fill: var(--hover-color);\n}\n\n.pc-line {\n    stroke: black;\n    stroke-width: 1px;\n    stroke-opacity: 0.3;\n    fill: none;\n}\n\n.pc-line.pc-missing-line {\n    stroke-dasharray: 4;\n    stroke-opacity: 0.2;\n}\n\n.pc-fat-line {\n    opacity: 0;\n    stroke-width: 7px;\n    fill: none;\n    cursor: pointer;\n    stroke: black;\n}\n\n.pc-selected .pc-line {\n    stroke-opacity: 1;\n    stroke-width: 3px;\n    stroke: var(--hover-color) !important;\n}\n\n.pc-highlighted .pc-line {\n    stroke-opacity: 1;\n    stroke-width: 2px;\n    stroke: var(--jp-brand-color1);\n}\n\n.pc-tooltip {\n    font-family: var(--jp-content-font-family);\n    display: none;\n    background-color: black;\n    color: white;\n    padding: 2px 6px;\n    margin: auto;\n    border-radius: 5px;\n    font-size: 11px;\n}\n\n.pc-highlighted:hover .pc-tooltip,\n.pc-selected:hover .pc-tooltip {\n    display: initial;\n}\n\n.pc-brush-overlay {\n    pointer-events: all;\n    fill: none;\n    cursor: crosshair;\n}\n\n.pc-brush-selection {\n    fill: #777;\n    fill-opacity: 0.3;\n    stroke: #fff;\n    pointer-events: none;\n}\n\n\n/** Help Icon **/\n.icon {\n    display: inline-flex;\n    align-self: center;\n}\n\n.icon.baseline svg {\n    top: .125em;\n    position: relative;\n    font-size: 1rem;\n    margin-left: 5px;\n}\n\n\n/** Surrogate Explanation **/\n.surrogate_explanation .pc-fat-line {\n    cursor: default;\n}\n\n\n/** HP Importance **/\n.hp-importance .recharts-rectangle:hover {\n    cursor: pointer;\n}\n\n.hp-importance_row:hover {\n    cursor: pointer;\n}\n\n\n/** Performance Overview **/\n.MuiTab-root.tab-small {\n    min-width: 120px;\n}\n\n.performance-wrapper {\n    display: flex;\n    flex-flow: wrap;\n    justify-content: space-around;\n}\n\n.performance-wrapper > div {\n    min-width: auto;\n    white-space: nowrap;\n    flex-basis: 1%;\n}\n\n\n/** Decision Surface **/\n.decision-surface {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: space-around;\n}\n\n\n/** Feature Importance **/\n.pdp .MuiInputBase-input {\n    font-size: var(--jp-code-font-size);\n    padding-top: 0;\n    line-height: unset;\n}\n\n\n/** Comparison **/\n.comparison-anchor {\n    position: relative;\n}\n\n.comparison-container {\n    padding: 10px;\n    width: 1000px;\n    position: absolute;\n    right: 0;\n    top: 0;\n    background-color: white;\n}\n\n\n.recharts-default-legend svg {\n    display: inline-block;\n    vertical-align: middle;\n    margin-right: 4px;\n}\n"],"sourceRoot":""}]);
